import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import TopNavBar from './components/reusable/Navbar';
import Footer from './components/reusable/Footer';
import Home from './pages/Home';
import Restaurant from './components/restaurant/Restaurant';
import ReviewModal from './components/modal/ReviewModal';
import RestaurantModal from './components/modal/RestaurantModal';
import env from "./env";

import initRestList from './initRestList';

const service = new window.google.maps.places.PlacesService(document.createElement("div"));

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      allRest: initRestList,
      filteredRest: [],
      userPos: { lat: 6.907074, lng: 79.894276 },
      expandedRest: {
        review: [],
        geometry: {
          location: {}
        }
      },
      newRestLocation: {},
      rateFilterValue: 0,
      priceFilterValue: 4,
      bounds: { latBounds: {}, lngBounds: {} },
      showReviewModal: false,
      showRestModal: false
    };
  }

  componentDidMount() {
    this.setUserLocation();
  }

  setRestaurants({ lat, lng }) {
    var request = {
      location: new window.google.maps.LatLng(parseFloat(lat), parseFloat(lng)),
      radius: '2000',
      query: 'restaurants',
      type: "restaurant"
    };

    service.textSearch(request, this.setRestCallback);
  }

  setRestCallback = (results, status) => {
    let { allRest, userPos } = this.state;

    if (status === window.google.maps.places.PlacesServiceStatus.OK) {
      let restaurantList = results.map(item => {
        return {
          ...item,
          rating: item.rating && item.rating > 0 && item.rating <= 5 ? item.rating : 1,
          price_level: item.price_level && item.price_level > 0 && item.price_level <= 4 ? item.price_level : (Math.floor(Math.random() * 4) + 1),
          geometry: {
            location: {
              lat: item.geometry ? item.geometry.location.lat() : userPos.lat,
              lng: item.geometry ? item.geometry.location.lng() : userPos.lng,
            }
          }
        }
      });

      restaurantList.forEach(rest => {
        if (allRest.findIndex(restau => restau.place_id === rest.place_id) === -1) {
          allRest.push(rest);
        }
      });

      let newRestList = allRest.sort((a, b) => a.rating > b.rating ? -1 : a.rating < b.rating ? 1 : 0);
      this.setState({ allRest: newRestList, filteredRest: newRestList }, () => this.filterRestaurants(this.state.priceFilterValue, this.state.rateFilterValue, this.state.bounds));
    }
  }

  setUserLocation() {
    navigator.geolocation.getCurrentPosition(currPos => {
      this.setState({ userPos: { lat: currPos.coords.latitude, lng: currPos.coords.longitude } }, () => this.setRestaurants(this.state.userPos));
    }, (error) => {
      console.error(error);
    }, { timeout: 20000 });
  }

  changePrice(priceFilterValue) {
    this.setState({ priceFilterValue }, () => this.filterRestaurants(this.state.priceFilterValue, this.state.rateFilterValue, this.state.bounds));
  }

  changeRate(rateFilterValue) {
    this.setState({ rateFilterValue }, () => this.filterRestaurants(this.state.priceFilterValue, this.state.rateFilterValue, this.state.bounds));
  }

  changeBounds(bounds) {
    this.setState({ bounds }, () => this.filterRestaurants(this.state.priceFilterValue, this.state.rateFilterValue, this.state.bounds))
  }

  filterRestaurants(priceVal, rateVal, mapBounds) {
    let filteredList = this.state.allRest.filter(rest => {
      return rest.price_level <= priceVal &&
        rest.rating >= rateVal &&
        (rest.geometry.location.lat >= mapBounds.latBounds.lower && rest.geometry.location.lat <= mapBounds.latBounds.upper) &&
        (rest.geometry.location.lng >= mapBounds.lngBounds.lower && rest.geometry.location.lng <= mapBounds.lngBounds.upper)
    });
    this.setState({ filteredRest: filteredList });
  }

  selectRestaurant(place_id) {
    let expandedRest = this.state.allRest.find(rest => rest.place_id === place_id);

    var request = {
      placeId: expandedRest.place_id
    };

    service.getDetails(request, (place, status) => this.selectRestCallback(place, status, expandedRest));
  }

  selectRestCallback(place, status, expandedRest) {
    if (status === window.google.maps.places.PlacesServiceStatus.OK) {

      let reviewList = place.reviews;

      if (!expandedRest.reviews) {
        expandedRest.reviews = reviewList
      }

      this.setState({ expandedRest });
    }
  }

  clearRestSelection() {
    this.setState({
      expandedRest: {
        reviews: [],
        geometry: {
          location: {}
        }
      }
    });
  }

  clickMap(newRestLocation) {
    let { allRest } = this.state;
    let restIndex = allRest.findIndex(rest => rest.geometry.location.lat === newRestLocation.lat && rest.geometry.location.lng === newRestLocation.lng);
    if (restIndex === -1) {
      this.setState({ newRestLocation }, () => this.showRestModal());
    } else {
      alert("A restaurant already exists in this place");
    }
  }

  moveMap(userPos) {
    this.setState({ userPos }, () => this.setRestaurants(this.state.userPos))
  }

  showReviewModal() {
    this.setState({ showReviewModal: true });
  }

  hideReviewModal() {
    this.setState({ showReviewModal: false });
  }

  showRestModal() {
    this.setState({ showRestModal: true });
  }

  hideRestModal() {
    this.setState({ showRestModal: false, newRestLocation: {} });
  }

  addReview(location, author_name, text, rating) {
    let { allRest } = this.state;

    let placeIndex = allRest.findIndex(rest => rest.geometry.location.lat === location.lat && rest.geometry.location.lng === location.lng);

    if (placeIndex > -1) {
      allRest[placeIndex].reviews.push({ rating, author_name, text });
      this.setState({ allRest, expandedRest: allRest[placeIndex] });
    }
  }

  addRestaurant(name, price_level) {
    let { allRest, newRestLocation } = this.state;
    let { lat, lng } = newRestLocation;

    fetch("https://maps.googleapis.com/maps/api/geocode/json?latlng=" + lat + "," + lng + "&key=" + env.googleApi + "&origin=*")
      .then(response => response.json())
      .then(placesData => {
        let placesObj = placesData.results[0];
        let { formatted_address, place_id } = placesObj;
        let newRestaurant = { name, formatted_address: formatted_address, price_level, place_id: place_id, rating: 1, image: "https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80", geometry: { location: { ...newRestLocation } }, reviews: [] }
        allRest.push(newRestaurant);
        this.setState({ allRest, expandedRest: newRestaurant }, () => this.filterRestaurants(this.state.priceFilterValue, this.state.rateFilterValue, this.state.bounds));
      }).catch(error => console.error(error));
  }

  render() {
    return (
      <div className="App">
        <Restaurant expandedRest={this.state.expandedRest} onSelectClear={this.clearRestSelection.bind(this)} onAddReview={this.showReviewModal.bind(this)} />
        <TopNavBar onPriceChange={this.changePrice.bind(this)} onRateChange={this.changeRate.bind(this)} />
        <Home userPos={this.state.userPos} allRest={this.state.filteredRest} onRestSelect={this.selectRestaurant.bind(this)} onMapClick={this.clickMap.bind(this)} onMapMove={this.moveMap.bind(this)} onMapBoundChange={this.changeBounds.bind(this)} />
        <ReviewModal show={this.state.showReviewModal} onReviewClose={this.hideReviewModal.bind(this)} onReviewSubmit={this.addReview.bind(this)} restaurant={this.state.expandedRest} />
        <RestaurantModal show={this.state.showRestModal} onRestClose={this.hideRestModal.bind(this)} onRestSubmit={this.addRestaurant.bind(this)} />
        {/* <Footer /> */}
      </div>
    );
  }
}

export default App;
